import lodash from 'lodash'

const { differenceWith, each, fromPairs, has, isEqual, pickBy, toPairs } = lodash

const getDiffByStatus = (object1, object2): object => {
  const changes = fromPairs(differenceWith(toPairs(object2), toPairs(object1), isEqual)) as object

  const added = pickBy(changes, (value, key) => {
    return !has(object1, key)
  })

  const changed = pickBy(changes, (value, key) => {
    return has(object1, key)
  })

  const reverseChanges = fromPairs(differenceWith(toPairs(object1), toPairs(object2), isEqual)) as object

  const removed = pickBy(reverseChanges, (value, key) => {
    return !has(object2, key)
  })

  return { added: added, changed: changed, removed: removed }
}

const getDiff = (object1, object2) => {
  const diffByStatus = getDiffByStatus(object1, object2)

  const diff = {}

  each(diffByStatus, (value, statusKey) => {
    each(diffByStatus[statusKey], (value, key) => {
      diff[key] = {
        value: value,
        status: statusKey,
        previous: has(object1, key) ? object1[key] : null,
      }
    })
  })

  return diff
}

export { getDiff, getDiffByStatus }
