import lodash from 'lodash'

import { CATEGORIES } from '../constants'
import { sortObjectAlphabetically } from '../sortObjectAlphabetically'
import { getTransformationOptions } from './transformationOptions'
import { transformToNested } from './transformToNested'

const { each, get } = lodash

const helpTransformResponse = (response, callback = null) => {

  const transformed = {}

  each(response, (value, key) => {

    if (callback) {

      const {
        key: newKey,
        value: newValue,
      } = callback(key, value)

      if (newKey) {
        key = newKey
      }

      if (newValue) {
        value = newValue
      }
    }

    transformed[key] = value
  })

  return transformed
}

const transformResponse = (response, opts = {}, configs = null) => {

  const options = getTransformationOptions(opts)

  let transformed = response

  if (options.sort) {
    transformed = sortObjectAlphabetically(transformed)
  }

  if (options.categorized) {

    transformed = helpTransformResponse(transformed, key => {

      const config = get(configs, key)

      if (config) {
        key = `${CATEGORIES[config.category].plural}.${key}`
      } else {
        console.warn(`Non-existent key ${key} is not part of the config set.`)
        key = `nonexistent.${key}`
      }

      return {
        key,
      }
    })
  }

  if (options.nested) {
    return transformToNested(transformed)
  }

  return transformed
}

export { transformResponse }
