import lodash from 'lodash'

import { CATEGORIES } from '../constants'
import { getTransformationOptions } from './transformationOptions'
import { transformToNested } from './transformToNested'

const { each, get, has } = lodash

const transformFromConfigsToResponse = (configs, callback = null, defaultsOnly = false) => {

  const response = {}

  each(configs, (config, key) => {
    // Set inherited values
    if (config.inherit) {
      config.default = get(configs, config.inherit).default
    }
    // TODO: Figure out why configs have value properties when a configuration is saved but not published
    let value = has(config, `value`) && !defaultsOnly ? config.value : config.default

    if (callback) {

      const {
        key: newKey,
        value: newValue,
      } = callback(key, config)

      if (newKey) {
        key = newKey
      }

      if (newValue) {
        value = newValue
      }
    }

    response[key] = value
  })

  return response
}

const transformToConfigValues = (configs, opts = {}, defaultsOnly = false) => {

  const options = getTransformationOptions(opts)

  const response = transformFromConfigsToResponse(configs, (key, config) => {

    key = options.categorized ? `${CATEGORIES[config.category].plural}.${key}` : key

    return {
      key,
    }
  }, defaultsOnly)

  if (options.nested) {
    return transformToNested(response)
  }

  return response
}

export { transformFromConfigsToResponse, transformToConfigValues }
